@use '../setting' as *;
@use '../mixin' as *;

.first-view__inner {
    position: relative;
    height: rem(476);
}

.first-view__img-wrap {
    position: absolute;
    width: 100%;
    height: rem(476);
}

.first-view__img {
    width: 100%;
    max-height: rem(476);
    object-fit: cover;

    @include mq(max-md) {
        min-height: rem(476);
    }
}

.first-view__body {
    position: absolute;
    width: 100%;
    margin: rem(116) auto 0;

    @include mq(max-md) {
            margin-top: rem(50);
        }
}

.first-view__button {
    margin-top: rem(45);
}