@use '../setting' as *;
@use '../mixin' as *;

.question-toggle-checkbox {
    display: none;
}

.question-toggle-checkbox:checked ~ .question__item-description {
    display: block;
}


.question-toggle {
        width: 30px;
        height: 30px;
        cursor: pointer;
}


.question-toggle__line::after,
.question-toggle__line {
    display: inline-block;
    width: 100%;
    height: rem(3);
    background-color: colors(siteBlue);
    border-radius: 10px;
    transition: .3s;
}

.question-toggle__line::after {
        content: '';
        margin-bottom: 50%;
        transform: rotate(90deg);
}


.question-toggle-checkbox:checked + .question__item .question-toggle .question-toggle__line::after {
    transform: rotate(0deg);
}

.question-toggle-checkbox:checked + .question__item .question__text {
    margin-bottom: 0;
}
